@use "../config/g_prefix" as g;
@use "../config/color" as colors;
@use "sass:color";
@use "sass:list";

// ----------------------------------------
// Default colors like .bn-nutral-50, .bn-bg-nutral-50, .bn-b-nutral-50, etc...
// ----------------------------------------
$dark-color: false !default;
$dark-name: "";

@if ($dark-color ==true) {
    $dark-name: "dark\\:";
}

@else {
    $dark-name: "";
}

@each $name, $palette in colors.$shade-colors {

    $k : $name;

    @each $key, $value in $palette {
        // ----------------------------------------
        // solid colors like color: #fff, bg: #000, border:#f00
        // ----------------------------------------

        .#{$dark-name}#{g.$prefix-cls}-#{$name}-#{$key} {
            color: var(#{g.$prefix-colors}-#{$name}-#{$key});
        }

        .#{$dark-name}#{g.$prefix-cls}-bg-#{$name}-#{$key} {
            background-color: var(#{g.$prefix-colors}-#{$name}-#{$key});
        }

        .#{$dark-name}#{g.$prefix-cls}-b-#{$name}-#{$key} {
            border-color: var(#{g.$prefix-colors}-#{$name}-#{$key});
        }

        // ----------------------------------------
        // Hover colors like color-hover: #fff, bg-hover: #000, border-hover:#f00
        // ----------------------------------------
        .#{$dark-name}#{g.$prefix-cls}-#{$name}-#{$key}-hover:hover {
            color: var(#{g.$prefix-colors}-#{$name}-#{$key});
        }

        .#{$dark-name}#{g.$prefix-cls}-bg-#{$name}-#{$key}-hover:hover {
            background-color: var(#{g.$prefix-colors}-#{$name}-#{$key});
        }

        .#{$dark-name}#{g.$prefix-cls}-b-#{$name}-#{$key}-hover:hover {
            border-color: var(#{g.$prefix-colors}-#{$name}-#{$key});
        }

    }
}

// ----------------------------------------
// Custom colors like .link, .disabled, .success, .error, etc...
// ---------------------------------------- 

@each $key, $value in colors.$base-colors {

    // ----------------------------------------
    // solid colors like color: #fff, bg: #000, border:#f00
    // ----------------------------------------
    .#{$dark-name}#{g.$prefix-cls}-#{$key} {
        color: var(#{g.$prefix-colors}-#{$value});
    }

    .#{$dark-name}#{g.$prefix-cls}-bg-#{$key} {
        background-color: var(#{g.$prefix-colors}-#{$value});
    }

    .#{$dark-name}#{g.$prefix-cls}-b-#{$key} {
        border-color: var(#{g.$prefix-colors}-#{$value});
    }

    // ----------------------------------------
    // Hover colors like color-hover: #fff, bg-hover: #000, border-hover:#f00
    // ----------------------------------------
    .#{$dark-name}#{g.$prefix-cls}-#{$key}-hover:hover {
        color: var(#{g.$prefix-colors}-#{$value});
    }

    .#{$dark-name}#{g.$prefix-cls}-bg-#{$key}-hover:hover {
        background-color: var(#{g.$prefix-colors}-#{$value});
    }

    .#{$dark-name}#{g.$prefix-cls}-b-#{$key}-hover:hover {
        border-color: var(#{g.$prefix-colors}-#{$value});
    }

}